<script>
  export let name;
  export let desc;
  export let img;
  export let link;
</script>

<div class="main">
  <h3 class="name">{name}</h3>
  <img src={img} alt={name} />
  <div class="text-row">
    <h6>{desc}</h6>
    <h6>Click <a href={link}>here</a> to view live.</h6>
  </div>
</div>

<style>
  .name {
    text-align: center;
  }
  .main {
    color: #fff;
    background: #011d44;
    padding: 1em;
    margin: 10px auto;
    border-radius: 20px 20px 0 0;
    max-width: 900px;
    animation: appear 1s;
  }
  img {
    width: 400px;
    display: block;
    margin: auto;
  }
  .text-row {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    height: 130px;
  }
  .text-row h6 {
    line-height: 1.3em;
  }
  @media (max-width: 576px) {
    img {
      width: 260px;
    }
    .name {
      font-size: 30px;
    }
  }
  @media (min-width: 768px) {
    .text-row {
      min-height: 140px;
    }
  }
  @keyframes appear {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
</style>
