<script>
  import { onMount } from "svelte";
  import Typewriter from "typewriter-effect/dist/core";
  var main;
  onMount(() => {
    var typewriter = new Typewriter(main, {
      loop: true,
      delay: 75,
      deleteSpeed: 10,
    });
    typewriter
      .pauseFor(200)
      .typeString("Hi!")
      .pauseFor(1000)
      .deleteChars(3)
      .typeString("I'm Binong")
      .pauseFor(1000)
      .deleteChars(9)
      .typeString(" build things")
      .pauseFor(1000)
      .deleteChars(14)
      .typeString("For the Web")
      .pauseFor(1000)
      .start();
  });
</script>

