        <br />
        <h6>
          Interested in learning new skills and improving upon existing ones.
          Mainly Interested in Web Development, App Development and Software
          Development.
        </h6>
        <br />
        <h6>Currently learning Svelte and Flutter.</h6>
      </div>
      <div class="left-part">
        <img class="circle image" src="./assets/img/me.png" alt="me" />
        <div class="icon-set">
          <h6 style="text-align: center;">You can find me on:</h6>
